import React, { useState } from "react";
import "./index2.css";
import AddListnew from "./AddListnew";

import DateTime from './DateTime';



const AddList =()=>
{

const[inputList,setinputList]=useState("");
const[Items,setItems]=useState([]);


const itemEvent=(event)=>
{
    setinputList(event.target.value);
}

const listofItem=()=>
{
setItems((oldItems)=>{
return [...oldItems,inputList];

});
setinputList(" ");
};
const deleteItems = (id) =>
{
    console.log("delete");


    setItems((oldItems)=>
    {
        return oldItems.filter((arrElem,index) => {
            return index !== id;
        });
    });
};




    return(
        <>
        <div className="main">
            <div className="center overflow-auto">
            <div class="container">
           <br/>
           <h1>Disappearing Tweet.</h1>
           <br/>

            <div className="same">
            <input 
           type="text" 
           
           placeholder="Write a Tweet"
           onChange={itemEvent}
           value={inputList}
                 />






           <DateTime/>








           <button type="submit" onClick={listofItem}> Tweet </button>
       </div>

            <br/>
            <br/>
            <ol>
                
                {Items.map((itemval,index) => {
                    return<AddListnew  key={index}  id={index} text={itemval} onSelect={deleteItems} />
                })}
            </ol>
            
                  </div>
            </div>
        </div>
        </>

    );
};

export default AddList;